import React from 'react';

import { Navbar, NavItem, Icon } from 'react-materialize';

const Header = () => {
  return (
    <>
      <Navbar
        alignLinks="right"
        brand={
          <a className="brand-logo" href="/home">
            Logo
          </a>
        }
        id="mobile-nav"
        menuIcon={<Icon>menu</Icon>}
        options={{
          draggable: true,
          edge: 'left',
          inDuration: 250,
          onCloseEnd: null,
          onCloseStart: null,
          onOpenEnd: null,
          onOpenStart: null,
          outDuration: 200,
          preventScrolling: true,
        }}
      >
        <NavItem href="/home">Getting started</NavItem>
        <NavItem href="/maps">Components</NavItem>
      </Navbar>
    </>
  );
};

export default Header;
